html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  background-image: url("../img/guati.webp");
  background-repeat: no-repeat;
  background-size: cover;
}

p {
  font-size: 2rem;
  font-weight: 500;
  margin: 0;
  text-align: justify;
  font-family: "Inter", sans-serif;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4 {
  margin: 0 0 5rem 0;
  cursor: default;
  font-weight: 700;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4.5rem;
}
@media (max-width: 600px) {
  h2 {
    font-size: 4rem !important;
  }
}

h3 {
  font-size: 4rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

h2 {
  font-size: 5.5rem;
  font-weight: 800;
}

main .container {
  margin: 5rem 5%;
  border-radius: 5px;
  background-color: #FFFFFF;
  border: 1.5px solid #cac9c9;
  width: auto;
  height: 100%;
}
main .container .header {
  background-color: #f8fafc;
  border-bottom: 1.5px solid #cac9c9;
  padding: 1.5rem;
}
main .container .header p {
  text-align: center;
}
main .container .info {
  text-align: center;
  padding: 2rem;
}

.header_img,
.header_img_login {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  cursor: pointer;
}

input,
select {
  padding: 8px;
  border-radius: 5px;
  border: 1.5px solid #cac9c9;
  border-style: solid;
}

header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  background-color: #FFFFFF;
  align-items: center;
  padding: 1rem 10rem;
  /* box-shadow: 5px 3px 3px v.$grisO; */
}
header a {
  text-decoration: none;
  color: darkgray;
  margin-right: 2rem;
}
header a:hover {
  color: #181818;
}
header picture {
  display: flex; /*alinear vertical y horizontalmente*/
  height: 40px;
  width: 40px;
}

nav {
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: #6ab646;
}
nav .links {
  width: 80%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
nav .links a {
  text-decoration: none;
  color: #FFFFFF;
  font-size: 1.5rem;
  margin-right: 2rem;
  padding: 0.5% 0;
}
nav .links a:hover {
  color: #cac9c9;
}

#cerrar {
  color: #FF0000;
}

.info .input {
  margin-right: 25%;
  margin-bottom: 2rem;
  display: flex;
  justify-content: right;
  align-items: center;
}
.info .input input,
.info .input select {
  width: 50%;
  margin-left: 3rem;
}
.info button {
  background-color: #6ab646;
  padding: 1.2rem 2rem;
  border: none;
  border-radius: 5px;
  color: #FFFFFF;
}
.info button:hover {
  background-color: #559138;
}

.menu_principal {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.menu_principal .mp_btn,
.menu_principal #cerrar_sesion {
  margin: 0 5rem;
  padding: 1.5rem 0;
  width: auto;
  background-color: #6ab646;
  color: #FFFFFF;
  margin-bottom: 1.2rem;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 500;
  font-size: 2rem;
}
.menu_principal .mp_btn:hover {
  background-color: #559138;
}
.menu_principal #cerrar_sesion {
  background-color: #FF0000;
  margin-top: 2rem;
}
.menu_principal #cerrar_sesion:hover {
  background-color: #cc0000;
}

.cont {
  display: flex;
  justify-content: space-evenly;
}
.cont .cont_gen {
  width: 45%;
}
.cont .btn_comp {
  background-color: #6ab646;
  color: #FFFFFF;
  padding: 2%;
  width: 100%;
}
.cont .btn_rep {
  background-color: #6ab646;
  color: #FFFFFF;
  padding: 2%;
  width: 100%;
}
.cont .btn_delete_comp {
  background-color: #FF0000;
  color: #FFFFFF;
  cursor: pointer;
  padding: 0.5rem 1.5rem;
}
.cont .btn_delete_comp:hover {
  background-color: #cc0000;
}
.cont .btn_delete_rep {
  background-color: #FF0000 !important;
  color: #FFFFFF !important;
  cursor: pointer;
  padding: 0.5rem 1.5rem;
}
.cont .btn_delete_rep:hover {
  background-color: #cc0000;
}
.cont .th_comp {
  width: 10%;
}
.cont .th_borrar {
  width: 7%;
}

.delete_maq {
  background-color: #FF0000 !important;
  color: #FFFFFF !important;
  cursor: pointer;
  padding: 0.5rem 1.5rem;
}

#agregar_maqn {
  margin: 0 5%;
}
#agregar_maqn .flex {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
  gap: 5%;
  justify-content: center;
}
#agregar_maqn .grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  gap: 3%;
  justify-content: center;
}
#agregar_maqn input {
  width: 100%;
}
#agregar_maqn .input {
  display: grid;
  grid-template-columns: 20% 80%;
  margin-right: 0;
}
#agregar_maqn p {
  text-align: center;
  margin-bottom: 3%;
  margin-top: 1%;
}
#agregar_maqn #p_issue {
  margin: 5% 0 3% 0;
}
#agregar_maqn .input_radio {
  display: flex;
  flex-direction: column;
}
#agregar_maqn .radios {
  display: flex;
  margin-top: 5%;
}

#agregar_soli .radio_buttons {
  margin-bottom: 2%;
}
#agregar_soli .radios {
  margin-top: 1%;
  display: flex;
  justify-content: center;
  gap: 3%;
}

.calendario {
  margin: 0 5% !important;
}