@use "../base/variables" as v;
@use "base/mixins" as m;
.menu_principal {
  display: grid;
  grid-template-columns: repeat(3, 1fr);

  .mp_btn,
  #cerrar_sesion {
    margin: 0 5rem;
    padding: 1.5rem 0;
    width: auto;
    background-color: v.$verde;
    color: v.$blanco;
    margin-bottom: 1.2rem;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: 500;
    font-size: 2rem;
  }
  .mp_btn:hover {
    background-color: darken(v.$verde, 10);
  }
  #cerrar_sesion {
    background-color: v.$rojo;
    margin-top: 2rem;
  }
  #cerrar_sesion:hover {
    background-color: darken(v.$rojo, 10);
  }
}
